const algorithms = {
  maxTime: 50,

  decodePattern: (pattern) => {
    let split = pattern.split(',');
    return split.map((value) => {
      return {
        position: parseInt(value),
        legit: true
      }
    });
  },

  fcfs: (patternString) => {
    let pattern = algorithms.decodePattern(patternString);

    let dist = 0;
    let pos = 10;

    for (const patternElement of pattern) {
      dist += Math.abs(pos - patternElement.position)
      pos = patternElement.position;
    }
    return {distance: dist, order: [{position: 10}, ...pattern]};
  },

  sstf: (patternString) => {
    let pattern = algorithms.decodePattern(patternString);

    let order = [{position: 10}];

    let dist = 0;
    let pos = 10;

    while (pattern.length > 0) {
      let closest;
      for (const patternElement of pattern) {
        if (closest === undefined || Math.abs(closest.position - pos) > Math.abs(patternElement.position - pos)) {
          closest = patternElement;
        }
      }

      let index = pattern.indexOf(closest);
      if (index !== -1) pattern.splice(index, 1);

      dist += Math.abs(pos - closest.position)
      pos = closest.position;

      order.push(closest);
    }


    return {distance: dist, order: order};
  },

  scan: (patternString) => {
    let pattern = algorithms.decodePattern(patternString);

    let order = [{position: 10}];

    let dist = 0;
    let pos = 10;

    let dir = 1;

    while (pattern.length > 0) {
      let closest;
      for (const patternElement of pattern) {
        if (dir * patternElement.position >= dir * pos && (closest === undefined || Math.abs(closest.position - pos) > Math.abs(patternElement.position - pos))) {
          closest = patternElement;
        }
      }

      if (closest === undefined) {
        if (dir === 1) {
          order.push({position: 49});
          dist += Math.abs(pos - 49);
          pos = 49;
        } else {
          order.push({position: 0});
          dist += pos;
          pos = 0;
        }

        dir *= -1;
        continue;
      }

      let index = pattern.indexOf(closest);
      if (index !== -1) pattern.splice(index, 1);

      dist += Math.abs(pos - closest.position)
      pos = closest.position;

      order.push(closest);
    }


    return {distance: dist, order: order};

  },

  look: (patternString) => {
    let pattern = algorithms.decodePattern(patternString);

    let order = [{position: 10}];

    let dist = 0;
    let pos = 10;

    let dir = 1;

    while (pattern.length > 0) {
      let closest;
      for (const patternElement of pattern) {
        if (dir * patternElement.position >= dir * pos && (closest === undefined || Math.abs(closest.position - pos) > Math.abs(patternElement.position - pos))) {
          closest = patternElement;
        }
      }

      if (closest === undefined) {
        dir *= -1;
        continue;
      }

      let index = pattern.indexOf(closest);
      if (index !== -1) pattern.splice(index, 1);

      dist += Math.abs(pos - closest.position)
      pos = closest.position;

      order.push(closest);
    }


    return {distance: dist, order: order};

  }
};

export default algorithms;
