<template>
  <v-app>
    <v-app-bar app>
      <v-toolbar-title class="headline">
        <span>Kettapööruste planeerijad</span>
      </v-toolbar-title>
    </v-app-bar>

    <v-snackbar
      :timeout="4000"
      v-model="error"
      color="error"
      top
    >
      {{ errorMessage }}
    </v-snackbar>

    <v-content>
      <v-container fluid style="height: 100%" class="ma-0">
        <v-row dense>
          <v-col cols="2" dense>
            <v-row
              v-for="n in algorithms.length"
              :key="n"

            >
              <v-col>
                <v-card width="100%"
                        :flat="selectedAlgorithm !== n - 1"
                        :raised="selectedAlgorithm === n - 1"
                        @click="select(n)"
                >
                  <div class="pt-4 pb-4" style="text-align: center">{{algorithms[n - 1].label}}
                  </div>
                </v-card>
              </v-col>
            </v-row>
          </v-col>
          <v-divider vertical></v-divider>
          <v-col cols="9" dense>
            <v-row dense justify="center">
              <v-col>
                <v-card>
                  <v-row dense>
                    <v-col>
                      <v-card-title class="ml-2">Vali või sisesta järjend</v-card-title>

                    </v-col>

                  </v-row>

                  <v-divider class="ml-4 mr-4"></v-divider>
                  <v-radio-group v-model="radioGroup" style="width: 100%">
                    <v-row dense
                           class="ma-2"
                           v-for="n in predefinedPatterns"
                           :key="n.label"

                    >
                      <v-col cols="4">
                        <v-radio
                          :value="n.pattern"
                        >
                          <template slot="label">
                          <span class="mr-12">
                            {{n.label}}
                          </span>

                          </template>
                        </v-radio>

                      </v-col>
                      <v-col cols="6">
                      <span>
                        {{n.pattern}}
                      </span>
                      </v-col>
                    </v-row>
                    <v-row dense
                           class="ma-2 mb-0"

                    >
                      <v-col cols="4">
                        <v-radio
                          value="custom"
                        >
                          <template slot="label">
                          <span class="mr-12">
                            Enda oma
                          </span>

                          </template>
                        </v-radio>

                      </v-col>
                      <v-col cols="5">
                        <v-text-field class="ma-0 pa-0" v-model="customPattern"
                                      @click="radioGroup='custom'">

                        </v-text-field>
                      </v-col>
                      <v-spacer></v-spacer>
                      <v-col no-gutters class="pb-4 mr-4" style="flex-flow: 0; flex-shrink: 1">
                        <v-btn color="green" text @click="save" outlined>Käivita</v-btn>
                      </v-col>
                    </v-row>
                  </v-radio-group>
                </v-card>
              </v-col>
            </v-row>
          </v-col>

        </v-row>

        <v-row justify="center" dense>
          <v-col align="center" ref="canvasWrapper">
            <canvas ref="drawing"></canvas>
          </v-col>
        </v-row>
      </v-container>
    </v-content>
  </v-app>
</template>

<script>
    import algorithms from './plugins/algorithms';

    export default {
        name: 'App',
        data: () => ({
            result: undefined,
            radioGroup: undefined,

            error: false,
            errorMessage: "",

            colors: ["#206F71",
                "#8BC9AE",
                "#FCEBA9",
                "#FCB06F",
                "#D94B50",
                "#D291BC",
                "#C1E7E3",
                "#FFDCF4",
                "#AFDAC1",
                "#B399D4"],
            colorIndex: 0,

            algorithms: [{label: "FCFS", func: algorithms.fcfs},
                {label: "SSTF", func: algorithms.sstf},
                {label: "SCAN", func: algorithms.scan},
                {label: "LOOK", func: algorithms.look}
            ],

            customPattern: "",

            selectedAlgorithm: 0,

            predefinedPatterns: [
                {
                    label: "Esimene",
                    pattern: "5,19,4,2,20,25,12"
                },
                {
                    label: "Teine",
                    pattern: "1,10,44,2,12,3,13,20"
                },
                {
                    label: "Kolmas",
                    pattern: "5,2,13,34,22,31,1,10,9"
                }
            ],

            labels: []
        }),

        methods: {
            save: function () {
                this.error = false;
                try {
                    let pattern;
                    if (this.radioGroup === "custom") {
                        pattern = this.customPattern;
                    } else {
                        pattern = this.radioGroup;
                    }
                    this.result = this.algorithms[this.selectedAlgorithm].func(pattern);

                    // this.labels = pattern.split(";").map((value, index) => String.fromCharCode(index + 65) + " " + value);

                    console.log(this.result);
                    // console.log(this.result.map(pattern => pattern.map(p => p === null ? p : p.label)));
                } catch (e) {
                    this.error = true;
                    this.errorMessage = e;
                    return;
                }

                this.draw(this.result);
            },

            draw: function (order) {
                let canvas = this.$refs["drawing"];
                let canvasWrapper = this.$refs["canvasWrapper"];
                canvas.width = Math.min(canvasWrapper.clientWidth - 24, 1400);


                const ctx = canvas.getContext("2d");

                let sideLen = Math.floor((canvas.width) / 55);
                ctx.fillStyle = "black";

                canvas.height = sideLen * (order.order.length + 6);

                ctx.font = "12px Arial";

                ctx.fillText("Summaarne teepikkus: " + order.distance, sideLen, canvas.height - 12);

                for (let j = 0; j < algorithms.maxTime; j++) {
                    let x = sideLen * (j + 0.2);
                    let y = sideLen * 1.8;
                    ctx.fillText(j, x, y);
                }

                for (let j = 0; j < algorithms.maxTime; j++) {
                    let x1 = sideLen * (j) + 1;
                    let x2 = sideLen + 1;
                    let y2 = sideLen + 1;
                    let y1 = sideLen * 2.3;


                    ctx.fillStyle = "white";
                    ctx.fillRect(x1, y1, x2, y2);

                    ctx.beginPath();
                    ctx.rect(x1, y1, x2, y2);
                    ctx.stroke();
                    //
                    // ctx.fillStyle = "black";
                    // ctx.font = "10px Arial";
                    // ctx.fillText(processHistory[i][j] === null ? "-" : processHistory[i][j].label, x1 + sideLen / 2.4, y1 + sideLen / 1.5);
                    // console.log(x1, y1, x2, y2);

                }

                if (order.order.length === 1) {
                    return;
                }

                if (order.order[1].position === 10) {
                    order.order.splice(0, 1);
                }

                for (let i = 0; i < order.order.length; i++) {
                    let elem = order.order[i];

                    let x1 = sideLen * (elem.position  + 0.5);
                    let y1 = sideLen * (4 + i);

                    ctx.fillStyle = "black";

                    ctx.beginPath();
                    ctx.arc(x1, y1, 5, 0, 2 * Math.PI);
                    ctx.fill();

                    if (i > 0) {
                        let x2 = sideLen * (order.order[i - 1].position  + 0.5);
                        let y2 = sideLen * (4 + i - 1);
                        ctx.beginPath();
                        ctx.moveTo(x1, y1);
                        ctx.lineTo(x2, y2);
                        ctx.stroke();
                    }

                    if (elem.legit) {
                      ctx.fillText("X", x1 - 3, sideLen * 3.09);
                    }



                }

            },

            select: function (n) {
                this.selectedAlgorithm = n - 1;
                this.save();
            }
        },

        computed: {},

        created: function () {
            this.radioGroup = this.predefinedPatterns[0].pattern;
        },

        watch: {
            radioGroup: function () {
                this.save();
            }
        }

    };
</script>

<style>
  .v-input__control {
    width: 100% !important;
  }
</style>
